<template>
	<div class="list">
      <div class="list-item" v-for="item in cerList">
          <div class="item-left">
              <span class="img-cont"><img src="../assets/img/index-icon.png" alt=""></span>
                <strong class="item-tit">{{item.cert_name}}</strong>
          </div>
          <div class="item-right" :data-status="item.cert_status" @click='clickList(item.cert_id,item.cert_status,item.cert_name)'>
              <!--<span v-if="item.cert_status === 11" class="msg-default msg-primay">申请领取</span>-->
              <span v-if="item.cert_status == 2" class="msg-default">领取中</span><!--待审核/审核中-->
              <span v-else-if="item.cert_status == 3" class="msg-default msg-err">领取中</span>
              <span v-else-if="item.cert_status == 0" class="msg-default msg-err">未支付</span>
              <span v-else-if="item.cert_status == 4" class="msg-default msg-err">领取失败</span> <!--已拒绝-->
              <span v-else-if="item.cert_status == 5" class="msg-default msg-primay">查看证书</span>
              <span v-else-if="item.cert_status == 6" class="msg-default msg-err">领取失败</span><!--退款中-->
              <span v-else-if="item.cert_status == 7" class="msg-default msg-err">领取失败</span><!--已退款-->
              <span v-else class="msg-default">申请领取</span>
              <span class="img-icon"><img src="../assets/img/arrow.png" alt=""></span>
          </div>
      </div>
    </div>

</template>

<script>
export default {
  name: 'Home',
  data () {
      return {
        num:0,
        cerList:[],
        curCode:'',
        openid:'',
        token:''
      }
  },
  created:function(){
    this.getOpenid();
  },
  methods: {
    getOpenid(){
      let self = this;
      let access_code=this.GetQueryString('code');
      let local = window.location.href;
      // let url='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx6978819270b1b14b&redirect_uri=https://tax.caishuidai.com&response_type=code&scope=snsapi_userinfo&state=access#wechat_redirect';
      let url='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx6978819270b1b14b&redirect_uri=http://taxtest.xfrp.kunteng.org.cn&response_type=code&scope=snsapi_userinfo&state=access#wechat_redirect';

      if (access_code == null) {
        window.location.href = url;
      } else {
        self.curCode = access_code;
        self.$axios.get(self.baseUrl+'/api/weixin/openid/'+ self.curCode).then(res =>{
          //alert(JSON.stringify(res.data.data.openid));
          self.openid = res.data.data.openid;
          self.token = res.data.data.token;
          localStorage.setItem('openid',self.openid);

          self.getCerList();

        })
      }

    },
    getUserData(certlistdata){
      let self = this;
      self.$axios.get(self.baseUrl+'/api/v1/weixin/users/'+self.openid).then(res =>{
        if(res.data.success){
          //console.log(res.data.data.Certs);
          let certs = res.data.data.Certs;
          let certsList = certlistdata;
          for(let i in certs){
            //console.log(i,certs[i]);
            for(let st in certs[i]){
              //console.log(st,certs[i][st]);
              let cert_id = st;
              let cert_status = certs[i][st];
              for(let j in certsList){
                //console.log(j,certsList[j].cert_id,cert_id);
                if(certsList[j].cert_id == cert_id){
                  //console.log(certsList[j].cert_id);
                  certsList[j].cert_status = cert_status
                }else{
                  //certsList[j].cert_status = 11
                }
              }
            }

          }
          console.log(certsList);
          self.cerList = certsList;


        }else{
          // self.$toast.center(res.data.msg);
          self.cerList = certlistdata;
          console.log(res.data.msg);
        }

      })

    },
    getCerList(){
      let self = this;
      self.$axios.get(self.baseUrl+'/api/v1/weixin/certs').then(res =>{
        if(res.data.success){
          //self.cerList = res.data.data;
          self.getUserData(res.data.data);

        }else{
          self.$toast.center('数据获取失败');
        }

      })


    },
     increase() {
       this.num++
       this.setNum(this.num)      // 将this.num转入setNum
     },
    clickList: function(certid,status,certname){
      let self = this;
      // if(status == 6){//退款中
      //   this.$router.push({path:'/refund',query:{curcertid:certid,openid:this.openid}});
      // }
      if(status == 0){//未支付
        self.getOrderMsg(certid);
      }else if(status == 5){//已通过
        this.$router.push({path:'/lookcert'});
      }else{
         this.$router.push({path:'/SetForm',query:{curcertid:certid,curstatus:status,curcertname:certname}});
         // this.$router.push('/SetForm');
       };
    },
    GetQueryString(name){
      var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg)
      if(r!=null)return  unescape(r[2]); return null;
    },
    getOrderMsg(certid){
      let self = this;
      let openid = localStorage.getItem('openid');
      self.$axios({
        method:"get",
        url:self.baseUrl+'/api/v1/weixin/wxorder/'+ openid +'/'+ certid,
        // url:self.baseUrl+'/api/v1/weixin/wxorder/'+ openid,
      }).then((res)=>{
        if(res.data.success){
          let data = res.data.data;
          let appid = data.appid;
          let prepayid = data.prepay_id;
          let price = data.price;
          let name = data.name;
          let apikey = data.apikey;
          let orderid = data.orderid;
          if(prepayid === '0'){
            this.$router.push({path:'/Wait'});
          }else{
            this.$router.push({path:'/Order',query:{appid:appid,prepayid:prepayid,price:price,name:name,orderid:orderid,apikey:apikey}});
          }

        }else{
          self.$toast.center(res.data.msg);
        }
      })


    },




   },
   computed: {

   }




}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/******************list页 start**********************/
.list{
    width:100%;
    margin-top:20px;
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    background-color:#fff;
    border-top:1px solid #f3f3f5;/*no*/
}
.list-item{
    width:100%;
    height:130px;
    line-height:130px;
    padding:0 30px;
    border-bottom:1px solid #f3f3f5;/*no*/
    display: -webkit-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
}
.item-left{

}
.item-right{

}
.item-tit{
    font-size:28px;
    color:#333;
    font-weight: normal;
    padding-left:10px;
}
.msg-default{
    font-size:24px;
    color:#aaa;
    padding-right:10px;
}
.msg-err{
    color:#e83535;
}
.msg-primay{
    color:#d19421;
}
.img-cont{
    width:86px;
    height:86px;
    vertical-align: middle;
}
.img-icon{
    width:12px;
    height:21px;
    /*vertical-align: middle;*/

}
img{
    width:100%;
    height:100%;
}
</style>



